.blogs {
  margin-left: 30px;

  .blog-list {
    margin: 0;
    padding: 0;
    list-style-type: none;

    li {
      line-height: 30px;
      padding: 10px;
    }
  }

  .grid-blog {
    display: grid;
    grid-template-columns: 4fr 1fr;
  }

  .col-2 {
    -ms-flex-item-align: center;
        align-self: center;
  }
}



